import logo from "./logo.svg";
import "./App.css";
import React from 'react';
import { BrowserRouter, HashRouter, Switch, Route, Link } from "react-router-dom";
import TaskBoard from "./component/TaskBoard";
import TaskAppBar from "./component/TaskAppBar";
import CleaningRequest from "./component/CleaningRequest";
import TaskView from "./component/TaskView";
import { ThemeProvider, createTheme } from "@material-ui/core";
import { createGlobalStyle } from "styled-components";
import CleaningRequestList from "./component/CleaningRequestList";
import Footer from "./component/Footer";
import Chip from '@mui/material/Chip';
import Stack from '@mui/material/Stack';

function App() {
  const GlobalStyles = createGlobalStyle`body { font-family: 'Oswald';  }  `;

  const theme = createTheme({
    palette: {
      primary: {
        main: "#aa8f00",
      },
      secondary: {
        main: "#ddcc80",
      },
    },
    overrides: {
      MuiTab: {
        root: {
          fontWeight: 800,
          color: "red",
        },
      },
    },
  });

  const currentURL = window.location.href; // returns the absolute URL of a page
  const pathname = window.location.pathname; 


  console.log("pathname", currentURL);
  console.log("pathname", pathname);
  
  return (
    <ThemeProvider theme={theme}>
      <React.Fragment>
        <TaskAppBar theme={theme} />
        <BrowserRouter basename="/cleaning">
          <div>
            <Switch>
              <Route exact path="/" component={TaskBoard} />
              <Route path="/create-request" component={CleaningRequest} />
              <Route path="/create-plan" component={CleaningRequest} />
              <Route path="/request" component={CleaningRequestList} />
              <Route path="/tasks" component={TaskBoard} />
              <Route path="/create-task" component={TaskView} />
            </Switch>
          </div>
        </BrowserRouter>
        <Footer />
      </React.Fragment>
    </ThemeProvider>
  );
}

export default App;


