import * as React from "react";
import { styled } from "@mui/material/styles";
import Typography from "@mui/material/Typography";
import Box from "@mui/material/Box";
import Button from "@mui/material/Button";
import Grid from "@mui/material/Grid";
import Paper from "@mui/material/Paper";
import { makeStyles } from "@material-ui/styles";
import { fontSize } from "@mui/system";

const FooterContainer = styled(Paper)(() => ({
  textAlign: "center",
  position: "absolute",
  bottom: "0",
  color: "red",
  width: "100%",
  height: "70",
  background: "rgb(255, 209, 13)",
}));

const Item = styled(Paper)(({ theme }) => ({
  padding: theme.spacing(1),
  textAlign: "center",
  color: theme.palette.text.secondary,
}));

export default function Footer() {
  return (
    <FooterContainer>
      <Box>
        <Grid container spacing={2} id="toolbar">
          <Grid
            item
            xs={8}
            style={{ display: "flex", justifyContent: "center" }}
          >
            <Item
              style={{ backgroundColor: "rgb(255, 209, 13)", boxShadow: "" }}
              className="C"
              Id="phrase-footer"
            >
              <Typography variant="body2" fontSize="2em" fontFamily="Oswald">
                Помогаем экономить на уборке
              </Typography>
              <Typography variant="body2" fontFamily="Oswald">
                v.1.0.5
              </Typography>
            </Item>
          </Grid>
          <Grid
            item
            xs={4}
            style={{ display: "flex", justifyContent: "center" }}
          >
            <Item
              style={{
                backgroundColor: "rgb(255, 209, 13) ",
                boxShadow: "",
                marginBottom: "2em",
              }}
            >
              <Button size="small" color="secondary" variant="contained">
                Заказать домонстрацию / Задать вопрос
              </Button>
            </Item>
          </Grid>
        </Grid>
      </Box>
    </FooterContainer>
  );
}
